import axios from "axios";
import React, { Component, Fragment } from "react";
import { BiExport, BiSort } from "react-icons/bi";
import { FiEdit } from "react-icons/fi";
import { ImFilesEmpty } from "react-icons/im";
import {
  MdCheckBox,
  MdCheckBoxOutlineBlank,
  MdHistory,
  MdSearch,
} from "react-icons/md";
import { connect } from "react-redux";
import { RouteComponentProps } from "react-router";
import { Auth, PushPopupAlert } from "../../../actions";
import { GetAllSessionPlans } from "../../../actions/session";
import { PopupAlertData } from "../../../components/Alert/Popup";
import {
  generatePDF,
  reportSettingsInterface,
} from "../../../components/DownloadPDF/rest";
import Loading from "../../../components/Loading/Loading";
import TableLoading from "../../../components/Loading/TableLoading";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { SideNavigation } from "../../../components/Navigation/SideNavigation";
import { SessionPlanInterface } from "../../../interfaces";
import {
  centerReportContentInterface,
  centerReportContentKeyInterface,
  centerReportInterface,
  CenterToPrintInterface,
  getDistrictInRoom,
  getRoomOccupation,
  roomDistrictInterface,
  roomWithOccupation,
} from "../../../interfaces/center_report";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { SessionPlanReducerInterface } from "../../../reducers/session";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
} from "../../../shared/dataTest";
import { API } from "../../../utils/api";
import ExportToExcel from "../../../utils/ExportToExcel";
import { errorToText, sortThisArray } from "../../../utils/functions";
import DistrictList from "./DistrictList";

// TYPES OF REPORTS ======================================
export enum centerReportTitleType {
  ROOM_OCCUPATION = "Room occupation report", // Add course if needed (on course)
  ROOM_INVIGILATOR = "Room with invigilator report",
}

const defaultReportContent: centerReportContentInterface[] = [
  {
    key: centerReportContentKeyInterface.district_name,
    title: "District name",
    status: true,
  },
  {
    key: centerReportContentKeyInterface.center_name,
    title: "Center name",
    status: true,
  },
  {
    key: centerReportContentKeyInterface.room_number,
    title: "Room number",
    status: true,
  },
  {
    key: centerReportContentKeyInterface.room_type,
    title: "Room type",
    status: true,
  },
  {
    key: centerReportContentKeyInterface.room_capacity,
    title: "Room capacity",
    status: true,
  },
  {
    key: centerReportContentKeyInterface.room_occupation,
    title: "Room occupation",
    status: true,
  },
];

interface MatchParams {
  defaultReportType?: string;
}

interface CenterReportProps {
  //   REDUX ############################
  auth: Auth;
  sessionPlan: SessionPlanReducerInterface;
  GetAllSessionPlans: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface CenterReportState {
  loading: boolean;
  selectSession: boolean;
  selectAll: boolean;
  selectedReportTitle: centerReportTitleType;
  selectedSession: SessionPlanInterface | null;
  reportMainData: centerReportInterface[] | null;
  reportContentExport: centerReportContentInterface[];
  reportTitle: string;
  centerToPrint: CenterToPrintInterface[] | null;
  sortProperty: string;
  isSorted: boolean;
  sortOrder: "ASC" | "DESC";
  searchInput: string;
  generateReportModal: {
    reportType: "EXCEL" | "PDF";
    status: boolean;
  } | null;
  formError: {
    element: string;
    msg: string;
  } | null;
  selectedDistrict: roomDistrictInterface | "all";
  chooseDistrict: boolean;
}

class index extends Component<
  CenterReportProps & RouteComponentProps<MatchParams>,
  CenterReportState
> {
  constructor(props: any) {
    super(props);
    this.state = {
      loading: true,
      selectSession: false,
      selectedReportTitle: centerReportTitleType.ROOM_OCCUPATION,
      selectedSession: null,
      reportMainData: null,
      reportContentExport: defaultReportContent,
      selectAll: true,
      reportTitle: "",
      centerToPrint: null,
      sortProperty: "",
      isSorted: false,
      sortOrder: "ASC",
      searchInput: "",
      generateReportModal: null,
      formError: null,
      selectedDistrict: "all",
      chooseDistrict: false,
    };
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };

  setSelectAll = (status: boolean) => {
    this.setState({ selectAll: status });
  };

  setSelectSession = (status: boolean) => {
    this.setState({ selectSession: status });
  };

  setSelectedReportTitle = (thisTitle: centerReportTitleType) => {
    this.setState({ selectedReportTitle: thisTitle });
  };

  setReportTitle = (thisTitle: string) => {
    this.setState({ reportTitle: thisTitle });
  };

  setSelectedSession = (thisSession: SessionPlanInterface) => {
    this.setState({ selectedSession: thisSession });
  };

  setDefaultReport = (thisReport?: string) => {
    if (thisReport && thisReport === "invigilator") {
      return this.setState({
        selectedReportTitle: centerReportTitleType.ROOM_INVIGILATOR,
      });
    }
    return this.setState({
      selectedReportTitle: centerReportTitleType.ROOM_OCCUPATION,
    });
  };

  setFormError = (
    data: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: data });
  };

  loadReportMainData = async (thisSession: SessionPlanInterface) => {
    try {
      const res = await axios.get<centerReportInterface[]>(
        `${API.APPLICATION}/currentroomoccupation/${thisSession.session_plan_id}`
      );
      console.log("====================================");
      console.log({ reportMainData: res.data });
      console.log("====================================");
      this.setState({ reportMainData: res.data });
    } catch (error) {
      console.error({ reportMainData_error: error });
      // callback(false, errorToText(error));
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  setSortOrder = (thisOrder: "ASC" | "DESC") => {
    this.setState({ sortOrder: thisOrder });
  };

  setSortProperty = (thisProperty: string) => {
    this.setState({ sortProperty: thisProperty });
  };

  setIsSorted = (status: boolean) => {
    this.setState({ isSorted: status });
  };

  setGenerateReportModal = (
    data: {
      reportType: "EXCEL" | "PDF";
      status: boolean;
    } | null
  ) => {
    this.setState({ generateReportModal: data });
  };

  componentDidMount = () => {
    this.setDefaultReport(this.props.match.params.defaultReportType);
    if (this.props.sessionPlan.allSessionPlans === null) {
      this.props.GetAllSessionPlans(
        (
          status: boolean,
          errorMsg: string,
          sessionData: SessionPlanInterface[]
        ) => {
          this.setLoading(status);
          if (!status) {
            if (!isEmptyOrSpaces(errorMsg)) {
              this.props.PushPopupAlert({
                title: errorMsg,
                theme: PopupAlertTheme.warning,
              });
            }

            if (sessionData && sessionData.length > 0) {
              let activeSession = sessionData.find((item) => item.is_active);
              if (activeSession) {
                this.setSelectedSession(activeSession);
                if (this.state.reportMainData === null) {
                  this.loadReportMainData(activeSession);
                }
              } else {
                this.setSelectedSession(sessionData[sessionData.length - 1]);
                if (this.state.reportMainData === null) {
                  this.loadReportMainData(sessionData[sessionData.length - 1]);
                }
              }
            }
          }
        }
      );
    } else {
      if (this.props.sessionPlan.allSessionPlans.length > 0) {
        let activeSession = this.props.sessionPlan.allSessionPlans.find(
          (item) => item.is_active
        );
        if (activeSession) {
          this.setSelectedSession(activeSession);
          if (this.state.reportMainData === null) {
            this.loadReportMainData(activeSession);
          }
        } else {
          this.setSelectedSession(
            this.props.sessionPlan.allSessionPlans[
              this.props.sessionPlan.allSessionPlans.length - 1
            ]
          );
          if (this.state.reportMainData === null) {
            this.loadReportMainData(
              this.props.sessionPlan.allSessionPlans[
                this.props.sessionPlan.allSessionPlans.length - 1
              ]
            );
          }
        }
        this.setLoading(false);
      }
    }
  };

  FC_FilterReportData = (
    allData: centerReportInterface[],
    district_id: string | "all",
    center_id: string | "all",
    course_id: string | "all"
  ): centerReportInterface[] => {
    let result: centerReportInterface[] = allData;
    if (district_id !== "all") {
      result = result.filter((item) => item.district_id === district_id);
    }
    if (center_id !== "all") {
      result = result.filter((item) => item.center_id === center_id);
    }

    if (course_id !== "all") {
      let _result: centerReportInterface[] = [];
      for (const key in result) {
        let check = result[key].course_in_room.find(
          (course) => course.course_id === course_id
        );
        if (check) {
          _result.push(result[key]);
        }
      }
      result = _result;
    }

    return result;
  };

  updateContentToPrint = () => {
    // UPDATE REPORT TITLE ========================
    if (this.state.selectedSession) {
      let title =
        "Center Report - " + this.state.selectedSession.session_plan_name;
      if (this.state.selectedDistrict !== "all") {
        title =
          "Center Report " +
          this.state.selectedDistrict.district_name +
          " district -" +
          this.state.selectedSession.session_plan_name;
      }
      this.setReportTitle(title);
    }
    // UPDATE CONTENT =============================
    if (this.state.reportMainData) {
      let arrayNew: CenterToPrintInterface[] = [];
      let n: number = 1;
      for (const key in this.state.reportMainData) {
        if (
          Object.prototype.hasOwnProperty.call(this.state.reportMainData, key)
        ) {
          let dataArray: CenterToPrintInterface | null = null;
          if (this.state.selectedDistrict !== "all") {
            if (
              this.state.selectedDistrict.district_id ===
              this.state.reportMainData[key].district_id
            ) {
              dataArray = {
                no: n,
                district_name: this.state.reportMainData[key].district_name,
                center_name: this.state.reportMainData[key].center_name,
                room_number: this.state.reportMainData[key].room_number,
                room_type: this.state.reportMainData[key].room_type,
                room_capacity: this.state.reportMainData[key].capacity,
                room_occupation: getRoomOccupation(
                  this.state.reportMainData[key]
                ),
              };
            }
          } else {
            dataArray = {
              no: n,
              district_name: this.state.reportMainData[key].district_name,
              center_name: this.state.reportMainData[key].center_name,
              room_number: this.state.reportMainData[key].room_number,
              room_type: capitalizeFirstLetter(
                this.state.reportMainData[key].room_type
              ),
              room_capacity: this.state.reportMainData[key].capacity,
              room_occupation: getRoomOccupation(
                this.state.reportMainData[key]
              ),
            };
          }

          for (const key in this.state.reportContentExport) {
            if (
              Object.prototype.hasOwnProperty.call(
                this.state.reportContentExport,
                key
              )
            ) {
              if (!this.state.reportContentExport[key].status && dataArray) {
                delete dataArray[this.state.reportContentExport[key].key];
              }
            }
          }
          dataArray && arrayNew.push(dataArray);
          n++;
        }
      }
      // if(this.state.selectedDistrict!=="all"){
      //   arrayNew = arrayNew.filter(item=>item.);
      // }
      this.setState({ centerToPrint: arrayNew });
      arrayNew = [];
      n = 1;
    }
  };

  updateReportContent = (key: centerReportContentKeyInterface) => {
    let index: number = this.state.reportContentExport.findIndex(
      (item) => item.key === key
    );
    let newArray: centerReportContentInterface[] =
      this.state.reportContentExport;
    newArray[index].status = !newArray[index].status;
    this.setState({ reportContentExport: newArray });
    let isSelectAllNeeded = newArray.find((item) => item.status === false);
    if (isSelectAllNeeded) {
      this.setSelectAll(false);
    } else {
      this.setSelectAll(true);
    }

    // UPDATE APPLICATION LIST FOR UPDATE ==============================
    this.updateContentToPrint();
  };

  isChangeAllowed = (key: centerReportContentKeyInterface): boolean => {
    if (
      key === centerReportContentKeyInterface.center_name ||
      key === centerReportContentKeyInterface.district_name ||
      key === centerReportContentKeyInterface.room_number ||
      key === centerReportContentKeyInterface.room_capacity ||
      key === centerReportContentKeyInterface.room_occupation
    ) {
      return false;
    }
    return true;
  };

  resetReportContent = (currentState: boolean) => {
    let newArray: centerReportContentInterface[] =
      this.state.reportContentExport;
    for (const key in newArray) {
      if (this.isChangeAllowed(newArray[key].key)) {
        newArray[key].status = !currentState;
      }
    }

    this.setState({ reportContentExport: newArray });
    this.setSelectAll(!currentState);
    this.updateContentToPrint();
  };

  FC_GeneratePDF = () => {
    if (!document.querySelectorAll("#report_content tbody tr").length) {
      this.props.PushPopupAlert({
        title: `Can't generate Report of empty Data`,
        theme: PopupAlertTheme.warning,
      });
      return false;
    }

    if (this.state.selectedSession) {
      const table: HTMLElement | null =
        document.getElementById("report_content");
      const reportSettings: reportSettingsInterface = {
        doneBy: this.props.auth.user.fname + " " + this.props.auth.user.lname,
        title: "Center Report",
        subjectTitle: "Room occupation",
        subject: "All room",
        period: this.state.selectedSession.session_plan_name,
        footer: "Online Recruitment",
        institute: "All institute",
      };
      table && generatePDF(reportSettings, table);
    }
  };

  setSort = (thisProperty: string) => {
    if (
      this.state.sortProperty === thisProperty &&
      this.state.sortOrder === "ASC"
    ) {
      this.setSortOrder("DESC");
    } else {
      this.setSortOrder("ASC");
    }
    this.setSortProperty(thisProperty);
    this.setIsSorted(true);
  };

  searchData = (
    objList: centerReportInterface[],
    text: string
  ): centerReportInterface[] => {
    if (undefined === text || text === "") return objList;
    return objList.filter((product) => {
      let flag;
      // let dataKeys = product;

      for (let prop in product) {
        flag = false;
        if (
          prop === "center_name" ||
          prop === "room_number" ||
          prop === "district_name" ||
          prop === "occupied" ||
          prop === "capacity" ||
          prop === "course_in_room"
        ) {
          if (product[prop]) {
            flag =
              product[prop]!.toString()
                .toLowerCase()
                .indexOf(text.toLowerCase()) > -1;
          }
        }
        if (flag) break;
      }
      return flag;
    });
  };

  setChooseDistrict = (status: boolean) => {
    this.setState({ chooseDistrict: status });
  };

  setSelectedDistrict = (data: roomDistrictInterface | "all") => {
    this.setState({ selectedDistrict: data });
  };

  render() {
    if (this.state.loading) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading data...
        </div>
      );
    }

    return (
      <Fragment>
        <SideNavigation>
          <div className="">
            <div className="text-gray-600 text-sm mb-2">Session:</div>
            <div
              onClick={() => this.setSelectSession(true)}
              className="font-bold bg-blue-50 border border-blue-200 rounded py-2 cursor-pointer text-xs px-2 hover:underline justify-between text-blue-600  mb-4 w-full flex flex-row items-center animate__animated animate__zoomIn"
            >
              {this.state.selectedSession !== null
                ? `${this.state.selectedSession.session_plan_name}`
                : "Filter by session"}
              <FiEdit className="text-lg text-blue-600" />
            </div>
          </div>

          <div className="">
            <div className="text-gray-600 text-sm mb-2">District:</div>
            {!this.state.chooseDistrict && this.state.reportMainData && (
              <div
                onClick={() => this.setChooseDistrict(true)}
                className="font-bold bg-blue-50 border border-blue-200 rounded py-2 cursor-pointer text-xs px-2 hover:underline justify-between text-blue-600  mb-4 w-full flex flex-row items-center animate__animated animate__zoomIn"
              >
                {this.state.selectedDistrict !== "all"
                  ? `${this.state.selectedDistrict.district_name}`
                  : "All district"}
                <FiEdit className="text-lg text-blue-600" />
              </div>
            )}
            {this.state.chooseDistrict && this.state.reportMainData && (
              <DistrictList
                setChooseDistrict={this.setChooseDistrict}
                districtList={getDistrictInRoom(this.state.reportMainData)}
                setSelectedDistrict={this.setSelectedDistrict}
                loadindRequest={this.state.loading}
                selectedDistrict={this.state.selectedDistrict}
              />
            )}
          </div>
        </SideNavigation>

        {/* BODY CONTENT--------------------------------- */}
        <section>
          {/* REPORT TITLE ============================================ */}
          <div className="grid md:grid-cols-12">
            <div className="col-span-8">
              <div className="mt-4 mx-4 flex flex-col gap-1">
                <span className="text-3xl font-bold text-gray-700">
                  Center report
                </span>
              </div>
            </div>
            <div className="col-span-4">
              <div className="float-right mt-2">
                {this.state.reportMainData &&
                  this.state.reportMainData.length > 0 && (
                    <div className="flex flex-row items-center justify-end font-bold mb-2">
                      <BiExport className="text-2xl text-gray-500" />
                      <span className="text-gray-500">Export as</span>
                      <button
                        className="px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200"
                        disabled={this.state.reportMainData ? false : true}
                        onClick={() => {
                          this.updateContentToPrint();
                          this.setGenerateReportModal({
                            reportType: "EXCEL",
                            status: true,
                          });
                        }}
                      >
                        EXCEL
                      </button>
                      <button
                        className="px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200"
                        onClick={() => this.FC_GeneratePDF()}
                        disabled={this.state.reportMainData ? false : true}
                      >
                        PDF
                      </button>
                    </div>
                  )}
              </div>
            </div>
          </div>
          {/* END REPORT TITLE ============================================ */}

          {/* ######################################################## */}
          <div className="bg-white my-4">
            <div className="w-full px-2 py-2">
              <input
                onChange={(e) => this.setState({ searchInput: e.target.value })}
                type="text"
                value={this.state.searchInput}
                placeholder="Search..."
                className="w-full bg-gray-100 py-2 px-3 rounded focus:border-gray-800"
                disabled={
                  this.state.reportMainData === null ||
                  this.FC_FilterReportData(
                    roomWithOccupation(this.state.reportMainData),
                    this.state.selectedDistrict !== "all"
                      ? this.state.selectedDistrict.district_id
                      : "all",
                    "all",
                    "all"
                  ).length <= 0
                }
              />
            </div>
            <table className="w-full shadow" id="report_content">
              <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                <tr>
                  <td className="px-2 py-2">#</td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("district")}
                  >
                    <div className="flex items-center gap-1">
                      District <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("center")}
                  >
                    <div className="flex items-center gap-1">
                      Center <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("room_number")}
                  >
                    <div className="flex items-center gap-1">
                      Room number <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("room_type")}
                  >
                    <div className="flex items-center gap-1">
                      Room type <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("capacity")}
                  >
                    <div className="flex items-center gap-1">
                      Room capacity <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("occupied")}
                  >
                    <div className="flex items-center gap-1">
                      Room occupation <BiSort />
                    </div>
                  </td>
                </tr>
              </thead>
              <tbody>
                {this.state.reportMainData === null ? (
                  <TableLoading cols={7} />
                ) : this.FC_FilterReportData(
                    roomWithOccupation(this.state.reportMainData),
                    "all",
                    "all",
                    "all"
                  ).length === 0 ? (
                  <tr>
                    <td colSpan={7} className="px-2 py-2">
                      <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                        <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                        <span className="text-sm font-bold">
                          No result found! Please try to search again
                        </span>
                        <div className="w-full mt-2 flex flex-row items-center justify-center">
                          <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                            <li
                              onClick={() => this.setSelectSession(true)}
                              className="underline text-gray-500 cursor-pointer"
                            >
                              Filter by session
                            </li>
                          </ul>
                        </div>
                      </div>
                    </td>
                  </tr>
                ) : (
                  sortThisArray(
                    this.state.isSorted,
                    this.searchData(
                      this.FC_FilterReportData(
                        roomWithOccupation(this.state.reportMainData),
                        "all",
                        "all",
                        "all"
                      ),
                      this.state.searchInput
                    ),
                    this.state.sortProperty,
                    this.state.sortOrder
                  ).map((item, i) => (
                    <tr
                      key={i + 1}
                      className={`${
                        i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                      } hover:bg-blue-100 hover:text-blue-700 border-b hover:border-blue-500 text-sm ${
                        item.room_type === "Default"
                          ? "border-gray-500 text-gray-700 bg-gray-200"
                          : "text-gray-600 border-gray-300"
                      }`}
                    >
                      <td className="px-2 py-2">{i + 1}</td>
                      <td className="px-2 py-2">{item.district_name}</td>
                      <td className="px-2 py-2">{item.center_name}</td>
                      <td className="px-2 py-2">{item.room_number}</td>
                      <td className="px-2 py-2">
                        {capitalizeFirstLetter(item.room_type)}
                      </td>
                      <td className="px-2 py-2">{item.capacity}</td>
                      <td className="px-2 py-2">{item.occupied}</td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </section>
        {/* END BODY CONTENT--------------------------------- */}

        {this.state.generateReportModal !== null &&
          this.state.generateReportModal.status === true && (
            <Modal
              title={
                <div className="flex flex-row gap-4 items-center">
                  {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
                  <div className="flex flex-col">
                    <span className="font-bold text-2xl text-gray-500">
                      Generating Report contents
                    </span>
                    <span className="text-sm text-blue-600 font-normal">
                      Generating {this.state.generateReportModal.reportType}{" "}
                      Report
                    </span>
                  </div>
                </div>
              }
              backDrop={true}
              theme={Themes.default}
              close={() => this.setGenerateReportModal(null)}
              backDropClose={false}
              widthSizeClass={ModalSize.medium}
              displayClose={true}
              padding={{ title: true }}
            >
              <div className="px-6">
                <div className="my-2 text-base text-gray-700 sm:text-lg">
                  <div className="flex flex-col">
                    <label>Title</label>
                    <input
                      type="text"
                      className={`text-left input-white w-20 inline-block ${
                        this.state.formError &&
                        this.state.formError.element === "report_title"
                          ? "border-red-500"
                          : "border-gray-500"
                      } border-2`}
                      placeholder="Sub Category Title"
                      value={this.state.reportTitle}
                      onChange={(e) => {
                        this.setFormError(null);
                        this.setReportTitle(e.target.value);
                      }}
                      disabled={this.state.reportMainData ? false : true}
                    />
                    {this.state.formError &&
                      this.state.formError.element === "report_title" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>
                  <div className="flex flex-col mt-2">
                    <label>Report content</label>
                    <div className="grid grid-cols-2 md:grid-cols-3 items-center gap-2 mt-2">
                      {this.state.reportContentExport.map((item, index) => (
                        <div
                          key={index}
                          className={`flex items-center gap-1 ${
                            this.isChangeAllowed(item.key)
                              ? "cursor-pointer"
                              : "cursor-not-allowed"
                          }`}
                          onClick={() =>
                            this.isChangeAllowed(item.key) &&
                            this.updateReportContent(item.key)
                          }
                          title={
                            !this.isChangeAllowed(item.key)
                              ? "Required for every report!"
                              : ""
                          }
                        >
                          {item.status ? (
                            <MdCheckBox className="w-6 h-6 text-blue-700" />
                          ) : (
                            <MdCheckBoxOutlineBlank className="w-6 h-6" />
                          )}
                          <span>{item.title}</span>
                        </div>
                      ))}
                    </div>
                    {this.state.formError &&
                      this.state.formError.element === "report_content" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>
                </div>
                <div className="flex justify-between mb-2 mt-4">
                  <div
                    className={`flex items-center gap-1 cursor-pointer`}
                    onClick={() =>
                      this.resetReportContent(this.state.selectAll)
                    }
                  >
                    {this.state.selectAll ? (
                      <MdCheckBox className="w-6 h-6 text-blue-700" />
                    ) : (
                      <MdCheckBoxOutlineBlank className="w-6 h-6" />
                    )}
                    <span>Select all</span>
                  </div>

                  {this.state.centerToPrint && this.state.reportMainData && (
                    <ExportToExcel
                      fileData={this.state.centerToPrint}
                      fileName={this.state.reportTitle}
                      btnName="Generate"
                    />
                  )}
                </div>
              </div>
            </Modal>
          )}
        {/* SELECT SESSION MODAL =========================== */}
        {this.state.selectSession && (
          <Modal
            title={
              <div className="flex flex-row gap-4 items-center">
                <MdHistory className="text-5xl text-blue-600" />
                <span className="flex flex-col">Choose session</span>
              </div>
            }
            backDrop={true}
            theme={Themes.default}
            close={() =>
              this.state.selectedSession !== null &&
              this.setSelectSession(false)
            }
            backDropClose={true}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={true}
            padding={{ title: true }}
          >
            <div className="px-2 pb-2">
              {this.props.sessionPlan.allSessionPlans == null ? (
                <Loading />
              ) : this.props.sessionPlan.allSessionPlans.length === 0 ? (
                <div className="flex flex-col text-gray-400 items-center justify-center">
                  <MdSearch className="text-6xl" />
                  <div className="flex flex-col items-center justify-center">
                    <span>No session found!</span>
                  </div>
                </div>
              ) : (
                this.props.sessionPlan.allSessionPlans.map((item, i) => (
                  <div
                    onClick={() => {
                      this.setSelectedSession(item);
                      this.loadReportMainData(item);
                      this.setSelectSession(false);
                    }}
                    key={i + 1}
                    className="grid grid-cols-12 bg-blue-50 border border-blue-200 rounded px-2 py-2 cursor-pointer hover:bg-blue-100 hover:border-blue-500 mb-3"
                  >
                    <div className="col-span-5">
                      <div className="text-blue-500 text-sm italic">
                        Session
                      </div>
                      <div className="font-bold">{item.session_plan_name}</div>
                      <div className="my-4"></div>
                      <div className="text-blue-500 text-sm italic">Status</div>
                      <div className="font-bold">
                        {item.is_active === true ? "Active" : "Expired"}
                      </div>
                    </div>
                    <div className="col-span-5">
                      <div className="text-blue-500 text-sm italic">Period</div>
                      <div className="font-bold">{item.period}</div>
                    </div>
                    <div className="col-span-2 flex items-center justify-end">
                      <div className="btn bg-blue-600 text-white mr-2">
                        Choose
                      </div>
                    </div>
                  </div>
                ))
              )}
            </div>
          </Modal>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  system_settings,
  sessionPlan,
}: StoreState): {
  auth: Auth;
  sessionPlan: SessionPlanReducerInterface;
} => {
  return {
    auth: auth,
    sessionPlan,
  };
};

export const CenterReport = connect(mapStateToProps, {
  GetAllSessionPlans,
  PushPopupAlert,
})(index);
